import {Table} from "antd";
import {useEffect, useState} from "react";
import "./RolesTable.css";

const RolesTable = (props) => {
    const [tableData, setTableData] = useState([])

    const columns = [
        {
            title: 'Игрок',
            dataIndex: 'player',
            key: 'player',
        },
        {
            title: 'Роль',
            dataIndex: 'role',
            key: 'role',
            render: (text, record) => <span className='roleLink' data-id={record.dataRole} onClick={props.showModal}>{text}</span>,
        },
        {
            title: 'Количество карт',
            dataIndex: 'cardNum',
            key: 'cardNum',
        },
        {
            title: 'Кол-во возвращений в игру',
            dataIndex: 'resNum',
            key: 'resNum',
        },
    ]

    function getRandomInt(min, max) {
        min = Math.ceil(min);
        max = Math.floor(max);
        return Math.floor(Math.random() * (max - min)) + min;
    }


    useEffect(() => {
        let data = [];
        for (let i = 0; i < props.playersQty; i++) {
            let randomRole = props.roles[getRandomInt(0, props.roles.length)];
            let newDataEntity = {
                key: i + 1,
                player: 'Игрок ' + (i + 1),
                role: randomRole.name,
                dataRole: randomRole.id,
                cardNum: randomRole.id === 0 ? 9 : getRandomInt(5, 8),
                resNum: randomRole.id === 0 ? 0 : getRandomInt(1, 3),
            }
            data.push(newDataEntity);
        }
        setTableData(data)
    },[])

    return (
        <Table pagination={false} columns={columns} dataSource={tableData}/>
    )
}

export default RolesTable;